import React, { memo, useCallback, useState } from 'react'

import BaseButton from 'src/components/BaseButton'
import InputRow from 'src/forms/InputRow'
import Input from 'src/forms/Input'

import Modal from '../Modal'
import styles from './styles.module.scss'

const SavePresetRaw = (props) => {
  const { onClose, onClickSave, presetName } = props
  const [value, setValue] = useState('')

  const onClickBtn = useCallback(() => {
    onClickSave && onClickSave({ value })
    onClose()
  }, [value, onClickSave, onClose])

  const onChangeInput = (value) => {
    setValue(value)
    console.log('onChangeInput', value)
  }

  return (
    <Modal title="Save preset" onClose={onClose} dialogStyles={styles.dialog}>
      <div className={styles.content}>
        <InputRow>
          <Input
            name={presetName}
            value={value}
            onChange={(value) => onChangeInput(value)}
            label="Preset name"
            placeholder="Enter the preset name"
          />
        </InputRow>
      </div>
      <div className={styles.action}>
        <BaseButton text="Save" skin="yellow" disabled={false} onClick={onClickBtn} />
      </div>
    </Modal>
  )
}

export const SavePreset = SavePresetRaw
export default memo(SavePreset)
