import React, { memo, useCallback } from 'react'
import BaseButton from 'src/components/BaseButton'

import Modal from '../Modal'
import styles from './styles.module.scss'

const DeletePresetRaw = (props) => {
  const { onClose, onClickDelete, presetName } = props

  const onClickBtn = useCallback(() => {
    onClickDelete && onClickDelete({ presetName })
    onClose()
  }, [onClickDelete, onClose, presetName])

  return (
    <Modal title="Delete preset" onClose={onClose} dialogStyles={styles.dialog}>
      <div className={styles.content}>
        <div className={styles.descr}>Do you want to delete a preset:</div>
        <div className={styles.value}>{presetName}</div>
      </div>
      <div className={styles.action}>
        <BaseButton text="Delete" skin="red" disabled={false} onClick={onClickBtn} />
      </div>
    </Modal>
  )
}

export const DeletePreset = DeletePresetRaw
export default memo(DeletePreset)
