import React, { memo, useMemo, useCallback, useState, useRef } from 'react'
import cn from 'classnames'

import { ReactComponent as ProfileIcon } from 'src/assets/images/icons/profile.svg'
import { ReactComponent as LogoutIcon } from 'src/assets/images/icons/logout.svg'
import { ReactComponent as DownIcon } from 'src/assets/images/icons/down.svg'

import icons from 'src/assets/images/contacts'
import { useOnClickOutside } from 'src/hooks'

import { useMappedActions } from './bindings'
import styles from './styles.module.scss'

const contactList = [
  {
    key: 'phone',
    value: '123456789',
    name: 'Back call',
    icon: 'PhoneIcon',
  },
  {
    key: 'whatsapp',
    value: '123456789',
    name: 'WhatsApp',
    icon: 'TelegramIcon',
  },
  {
    key: 'telegram',
    value: '123456789',
    name: 'Telegram',
    icon: 'WhatsappIcon',
  },
]

const ProfileRaw = () => {
  const actions = useMappedActions()
  const [isOpened, setIsOpened] = useState(false)
  const hasLogo = true
  const profileRef = useRef(null)

  useOnClickOutside(profileRef, () => {
    setIsOpened(false)
  })

  const getLink = useCallback((key, value) => {
    switch (key) {
      case 'phone': {
        return `tel:${value}`
      }
      case 'whatsapp': {
        return `https://wa.me/${value}`
      }
      case 'viber': {
        return `viber://add?${value}`
      }
      case 'telegram': {
        return `tg://resolve?domain=${value}`
      }

      default: {
        return '#'
      }
    }
  }, [])

  const onClickHead = useCallback(() => {
    setIsOpened(!isOpened)
  }, [isOpened, setIsOpened])

  const onClickLogout = useCallback(() => {
    actions.logout()
    setIsOpened(false)
  }, [setIsOpened, actions])

  const renderContacts = useMemo(() => {
    return contactList.map((item) => {
      const { key, value, name, icon } = item
      const Icon = icons[icon]

      return (
        <li className={styles.item} key={key}>
          <a href={getLink(key, value)} className={styles.link} title={key}>
            <Icon className={styles.linkIcon} />
            <div className={styles.linkTitle}>{name}</div>
          </a>
        </li>
      )
    })
  }, [getLink])

  return (
    <div className={styles.profile} ref={profileRef}>
      <div className={styles.head} onClick={onClickHead}>
        <div className={styles.user}>
          <div className={styles.logo}>
            {hasLogo ? (
              <img
                className={styles.img}
                src="https://st2.depositphotos.com/4312111/7307/v/600/depositphotos_73071389-stock-illustration-basketball-logo-design-template.jpg"
                alt=""
              />
            ) : (
              <div className={styles.empty}>
                <ProfileIcon />
              </div>
            )}
          </div>
          <div className={styles.name}>Konstantinopolskiy Konstantin</div>
        </div>
        <div
          className={cn(styles.arrow, {
            [styles.arrowActive]: isOpened,
          })}
        >
          <DownIcon />
        </div>
      </div>
      {isOpened && (
        <div className={styles.body}>
          <div className={styles.top}>
            <div className={styles.id}>
              <div className={styles.idTitle}>User ID</div>
              <div className={styles.idValue}>13948921989438</div>
            </div>
            <ul className={styles.list}>{renderContacts}</ul>
          </div>

          <div className={styles.action}>
            <button className={styles.logout} onClick={onClickLogout}>
              <LogoutIcon className={styles.logoutIcon} />
              <span className={styles.logoutText}>Logout</span>
            </button>
          </div>
        </div>
      )}
    </div>
  )
}

export const Profile = ProfileRaw
export default memo(Profile)
